html {
  --header-height: 60px;
  --blue-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

body {
  background-color: #e5e5e5;
  margin: 0;
}

.header {
  text-align: justify;
  height: var(--header-height);
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

img {
  width: 40px;
  height: 40px;
  margin-left: 50px;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

li {
  display: block;
}

.nav {
  text-transform: uppercase;
  color: #000;
  justify-content: right;
  align-items: center;
  margin-right: 50px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  display: flex;
}

.nav__link {
  height: var(--header-height);
  line-height: var(--header-height);
  font-size: 12px;
  font-weight: 500;
  display: block;
  position: relative;
}

li:hover .nav__link, .nav__link--blue {
  color: var(--blue-color);
}

.nav :not(:last-child) {
  margin-right: 20px;
}

a.is-active {
  position: relative;
}

a.is-active:after {
  content: "";
  background-color: var(--blue-color);
  color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

